import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import SelectedForm from '../views/SelectedForm.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/form',
    name: 'SelectedForm',
    component: SelectedForm,
  },
  {
    path: '/form/:id',
    name: 'Form',
    component: Home,
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
})

export default router
