<template>
  <div class="space-all">
    <div class="columns">
      <div class="column center">
        <div class="card">
          <div class="card-content">
            <div class="content">
              <b-field
                label="Seleccione el formulario"
                label-position="on-border"
              >
                <b-select
                  placeholder="Formularios"
                  expanded
                  v-model="selectedForm"
                  @input="goForm()"
                >
                  <option :value="f.id" v-for="(f, i) in forms" :key="i">{{
                    f.name
                  }}</option>
                </b-select>
              </b-field>
            </div>
          </div>
        </div>
      </div>
    </div>

    <b-loading
      :is-full-page="true"
      v-model="isLoading"
      :can-cancel="false"
    ></b-loading>
  </div>
</template>

<script>
import TableView from '@/components/Table.vue'
import ModalCreate from '@/components/ModalCreate.vue'

//Services
import { getAllForms } from '../services/formstack.srv'

export default {
  name: 'Home',
  data() {
    return {
      isComponentModalActive: false,
      isLoading: false,
      data: [],
      isEdit: false,
      dataEdit: {},
      search: '',
      forms: [],
      selectedForm: null,
    }
  },
  components: {
    TableView,
    ModalCreate,
  },
  methods: {
    openModelCreate() {
      this.isEdit = false
      this.dataEdit = {}
      this.isComponentModalActive = true
    },

    async listForms() {
      try {
        let forms = await getAllForms()
        this.forms = forms
      } catch (error) {
        console.log(error)
      }
    },

    goForm() {
      this.$router.push('/form/' + this.selectedForm)
    },
  },

  created() {
    this.listForms()
  },
}
</script>

