<template>
  <div class="space-all">
    <div class="columns">
      <div class="column">
        <p class="title is-3 card-header-title is-centered">{{ data.title }}</p>
        <div v-for="(q, i) in data.questions" :key="i">
          <p class="title is-4 card-header-title is-centered">{{ q.name }}</p>
          <pie-chart
            :chartdata="{
              labels: [...q.data.map((d) => d.name)],
              datasets: [
                {
                  label: q.name,
                  backgroundColor: [
                    'rgba(255, 99, 132, 0.4)',
                    'rgba(54, 162, 235, 0.4)',
                    'rgba(255, 206, 86, 0.4)',
                  ],
                  borderColor: [
                    'rgba(255, 99, 132, 1)',
                    'rgba(54, 162, 235, 1)',
                    'rgba(255, 206, 86, 1)',
                  ],
                  borderWidth: 1,
                  data: [...q.data.map((d) => d.res)],
                },
              ],
            }"
            :options="options"
          ></pie-chart>
          <br />
        </div>
      </div>
    </div>

    <b-loading
      :is-full-page="true"
      v-model="isLoading"
      :can-cancel="false"
    ></b-loading>
  </div>
</template>

<script>
import TableView from '@/components/Table.vue'
import ModalCreate from '@/components/ModalCreate.vue'
import PieChart from '@/components/Chart.vue'

//Services
import { calculateForm } from '../services/formstack.srv'

export default {
  name: 'Home',
  data() {
    return {
      isComponentModalActive: false,
      isLoading: false,
      data: [],
      isEdit: false,
      dataEdit: {},
      search: '',
      chartdata: {
        labels: ['January', 'February'],
        datasets: [
          {
            label: 'Data One',
            backgroundColor: [
              'rgba(255, 99, 132, 0.2)',
              'rgba(54, 162, 235, 0.2)',
            ],
            borderColor: ['rgba(255, 99, 132, 1)', 'rgba(54, 162, 235, 1)'],
            data: [40, 20],
          },
        ],
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
      },
    }
  },
  components: {
    TableView,
    ModalCreate,
    PieChart,
  },
  methods: {
    openModelCreate() {
      this.isEdit = false
      this.dataEdit = {}
      this.isComponentModalActive = true
    },
    async calculate() {
      try {
        this.data = await calculateForm(this.$route.params.id)
      } catch (error) {
        console.log(error)
      }
    },
  },

  created() {
    this.calculate()
  },
}
</script>

